@import url(../../node_modules/normalize.css/normalize.css);

html {
  position: relative;
  min-height: 100%;
  font-size: 16px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Ubuntu", Helvetica, Arial, sans-serif;
  margin: 0;
  padding: 0;
  width: 100%;
  min-height: 100%;
}

ul {
  list-style-type: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

.wrapper {
  position: relative;
  height: 100%;
  overflow: hidden;
}

.container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 95%;
  max-width: 58.75rem;
  height: 100%;
  margin: 0 auto;
}

.section {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}

.title {
  font-family: "Gagalin", Helvetica, Arial, sans-serif;
  color: #f9b43b;
  font-size: 4.5rem;
  margin-bottom: 20px;
}

.title_color-black {
  color: #2f3234;
}

.title_color-white {
  color: #fff;
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/Ubuntu-Light.woff2") format("woff2");
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Ubuntu-Regular.woff2") format("woff2");
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Ubuntu-Medium.woff2") format("woff2");
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Ubuntu-Bold.woff2") format("woff2");
}

@font-face {
  font-family: "Lifehack";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Lifehack-Medium.woff2") format("woff2");
}

@font-face {
  font-family: "Gagalin";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Gagalin-Regular.woff2") format("woff2");
}

.arrow-scroll {
  position: fixed;
  left: 50%;
  bottom: 2.5rem;
}

.arrow-scroll__icon {
  width: 1.812rem;
  height: 0.875rem;
}

.best {
  background: url(../img/bg-best.jpg) center/cover no-repeat;
}

.best__list {
  display: flex;
  margin-top: 2.4375rem;
}

.best__item {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 10px;
}

.best__desc {
  text-align: center;
}

.best__title {
  font-family: "Lifehack", Helvetica, Arial, sans-serif;
  font-size: 1.5rem;
  color: #2f3234;
  margin-bottom: 1.25rem;
}

.best__text {
  font-size: 14px;
  font-weight: 300;
  color: #2f3234;
  line-height: 1.8;
  opacity: .8;
}

.best__img {
  margin-bottom: 2.1875rem;
}

.best__icon {
  max-width: 96px;
  max-height: 89px;
}

.button {
  font-size: 0.8125rem;
  font-weight: 400;
  color: #fff;
  padding: 0.625rem 0.9375rem;
  border-radius: 0.3125rem;
  text-decoration: none;
  text-transform: uppercase;
  background-color: #e45028;
  transition: background-color .25s ease;
}

.button:hover {
  background-color: #c1401e;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  top: 1rem;
  right: 0;
  left: 0;
}

.header__nav {
  display: flex;
}

.hero {
  background: url(../img/bg-hero.jpg) center/cover no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero__container {
  display: flex;
}

.hero__content {
  display: flex;
  align-items: center;
}

.hero__text {
  display: flex;
  flex-direction: column;
  line-height: 1;
}

.hero__picture {
  margin-right: 1rem;
}

.hero__description {
  font-family: "Lifehack", Helvetica, Arial, sans-serif;
  font-size: 4.062rem;
  color: #fff;
  line-height: 1;
  letter-spacing: -1.63px;
}

.logo {
  display: block;
}

.logo__icon {
  width: 5.75rem;
  height: 4.3125rem;
}

.nav {
  display: flex;
  align-items: center;
  margin-right: 0.9375rem;
}

.nav__list {
  display: flex;
}

.nav__item {
  display: flex;
  align-items: center;
  margin-right: 0.9375rem;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__item::after {
  content: '';
  width: 5px;
  height: 5px;
  background-color: #fff;
  border-radius: 50%;
  margin-left: 0.9375rem;
}

.nav__item:last-child::after {
  display: none;
}

.nav__link {
  font-family: "Ubuntu", Helvetica, Arial, sans-serif;
  font-weight: 500;
  font-size: 0.8125rem;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  transition: color .25s ease;
}

.nav__link:hover {
  color: #c1401e;
}

.pagination {
  position: fixed;
  right: 3.125rem;
  top: 6.25rem;
  z-index: 2;
}

.pagination__list {
  display: flex;
  flex-direction: column;
}

.pagination__item {
  margin-bottom: 5px;
}

.pagination__item_active .pagination__link {
  border: 1px solid #fff;
}

.pagination__link {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid transparent;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  transition: border 0.3s cubic-bezier(0.64, 0.28, 0.48, 0.99);
}

.pagination__link::after {
  content: '';
  display: flex;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #fff;
}

.pagination__link:hover {
  border: 1px solid #fff;
}

@media (max-width: 768px) {
  .hero__content {
    flex-direction: column;
    text-align: center;
    width: 95%;
  }

  .hero__picture {
    margin: 0;
  }

  .hero__burger-img {
    max-width: 100%;
    height: auto;
  }

  .hero__description {
    padding: 0 3rem;
  }
}